import React from 'react';
import { useHistory } from "react-router-dom";
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
  CButton
} from '@coreui/react';

import usersData from './UsersData'
import profile from '../../assets/img/profile/profile1.png'

const getBadge = (status) => {
  switch (status) {
    case 'Active': return 'success'
    case 'Inactive': return 'secondary'
    case 'Pending': return 'warning'
    case 'Banned': return 'danger'
    default: return 'primary'
  }
}

const Users = () => {
  const history = useHistory()
  return (
    <div className="animated fadeIn">
      <h2>Users</h2>
      <CButton color="info" >Active </CButton>
      &nbsp;&nbsp;&nbsp;
      <CButton color="basic" > Cancelled </CButton>
      &nbsp;&nbsp;&nbsp;
      <CButton color="basic" > Total  </CButton>
      &nbsp;&nbsp;&nbsp;
      <CButton color="basic" > Banned  </CButton>
      <br />
      <br />
      <CRow>
        <CCol xl={12}>
          <CCard>
            <CCardHeader>
              Memberships
              {/* <small className="text-muted"> example</small> */}
            </CCardHeader>
            <CCardBody>
              <CDataTable
                items={usersData}
                fields={['profile',
                  { key: 'name', _classes: 'font-weight-bold' },
                  'username', 'DataRegistered', 'membership', 'role', 'subscription']}
                hover
                tableFilter
                itemsPerPageSelect
                striped
                pagination={{ doubleArrows: false, align: 'center' }}
                itemsPerPage={5}
                clickableRows
                onRowClick={(item, index) => history.push(`/users/${item.id}`)}
                scopedSlots={{
                  'profile':
                    (item) => (
                      <td><img src={item.profile} width="50px" height="50px" /></td>
                    ),
                  'subscription':
                    (item) => (
                      <td>
                        <CBadge color={getBadge(item.subscription)}>
                          {item.subscription}
                        </CBadge>
                      </td>
                    )
                }}
              />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </div>
  )
}

export default Users
